*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: black;
  } 

/* Navbar.css */

.navbar {
  background-color: gold;
  padding: 10px 20px;
  display: flex;
  justify-content: center;
}

.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin: 0 10px;
}

.nav-links a {
  color: black;
  text-decoration: none;
  font-size: 1.5rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

/* .nav-links a:hover {
  color: #f8f8f8;
} */


/* HomePage.css */

.home-page {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  top: 50% ; left: 50%;
  position: absolute;
  transform: translate(-50%,-50%);
}

h1 {
  font-size: 5rem;
  color: gold;
  margin-bottom: 10px;
  font-family: cursive;
}

.hbody {
  background-color: gold;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.sbody {
  font-size: 1.5rem;
  color: black;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.social-icons {
  margin-top: 4rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.social-icons a {
  color: gold;
  font-size: 2.5rem;
  margin: 0 10px;
  text-decoration: none;
}

.social-icons a:hover {
  color: #fff;
  transform: scale(1.3);
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeIn 1s ease-in-out; /* Adjust the animation duration as needed */
}

.ss{
  /* background-color: black; */
  color: black;
  margin-left: 2rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  text-decoration: none;
  font-size: 1.35rem;
  padding: 0.1rem;
}
.user-card{
  height: 50%;
  width: 80vw;
  margin: auto;
  background-color: gold;
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  border-radius: 1rem;
}
.yy{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}

select{
  width: 80vw;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 2rem;
  margin-top: 2rem;
  /* background-color: red; */
  border: none;
  outline: none;
  font-size: 1.1rem;
  
}