/* LoginForm.css */


  
  .lf {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 320px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    padding: 30px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
    transition: transform 0.3s ease-in-out;
  }
  
  form:hover {
    transform: scale(1.05);
  }
  
  label {
    display: block;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: bold;
    color: gold;
  }
  
  input {
    padding: 10px;
    border: none;
    border-radius: 6px;
    width: 100%;
    margin-bottom: 20px;
    background-color: rgba(255, 255, 255, 0.2);
    color: #fff;
    font-size: 16px;
    outline: none;
    transition: background-color 0.3s ease-in-out;
  }
  
  input:focus {
    background-color: rgba(255, 255, 255, 0.3);
  }
  
  button {
    padding: 12px 20px;
    background-color: gold;
    color: black;
    border: none;
    border-radius: 6px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }
  
  /* button:hover {
    background-color: rgb(0, 238, 255);
  } */
  
  @media (max-width: 360px) {
    form {
      width: 280px;
    }
  }
  